import React, { memo } from 'react';
import {
  Redirect, Route, Switch
} from 'react-router-dom';
import { compose } from 'recompose';
import { withStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import PropTypes from 'prop-types';

import { USER_MANAGER_ROLES } from '../../enums/user-roles';
import {
  MainLayout, withMainLayout
} from '../../components/MainLayout';
import withNotAuthorizedHandler from '../../utils/with-not-authorized-handler';
import ServicesMenu from './ServicesMenu';
import OnDemand from './OnDemand';
import Subscription from './Subscription';
import NewServiceRequest from './NewServiceRequest';
import styles from './styles';

const ListsHeaderBase = memo(({
  classes, setAlert, children
}) => (
  <React.Fragment>
    <div className={classes.container}>
      <div className={classes.headerView}>
        <aside>
          <Typography variant="h5">
            <b>Servios</b>
          </Typography>
        </aside>
      </div>
    </div>
    <div className={classes.container}>
      <ServicesMenu
        pathBase="/servicos"
      />
    </div>
    <div className={classes.container}>
      {React.cloneElement(children, { setAlert })}
    </div>
  </React.Fragment>
));
ListsHeaderBase.propTypes = {
  classes: PropTypes.object,
  setAlert: PropTypes.func,
  children: PropTypes.object
};
const ListsHeader = compose(
  withStyles(styles, { withTheme: true }),
  withMainLayout
)(ListsHeaderBase);

const Services = memo(() => (
  <Switch>
    <Route exact path="/servicos">
      <Redirect to={{ pathname: '/servicos/sobdemanda' }} />
    </Route>
    <Route exact path="/servicos/sobdemanda">
      <ListsHeader>
        <OnDemand />
      </ListsHeader>
    </Route>
    <Route exact path="/servicos/recorrentes">
      <ListsHeader>
        <Subscription />
      </ListsHeader>
    </Route>
    <Route path="/servicos/:serviceId/solicitar-servico">
      <MainLayout>
        <NewServiceRequest />
      </MainLayout>
    </Route>
  </Switch>
));

export default withNotAuthorizedHandler({
  condition: (viewer) => !USER_MANAGER_ROLES.includes(viewer.member.role)
})(Services);
