import React, { memo } from 'react';
import PropTypes from 'prop-types';
import { compose, withState } from 'recompose';
import { withRouter } from 'react-router';
import { Link } from 'react-router-dom';
import Button from '@material-ui/core/Button';
import { withStyles } from '@material-ui/core/styles';
import TextField from '@material-ui/core/TextField';
import Typography from '@material-ui/core/Typography';
import FormHelperText from '@material-ui/core/FormHelperText';
import CircularProgress from '@material-ui/core/CircularProgress';
import Grid from '@material-ui/core/Grid';
import { includes as _includes, values as _values,
  get as _get, mapValues as _mapValues } from 'lodash';
import styles from './styles';
import Session from '../../utils/session';
import config from '../../config';
import AlertBox from '../../components/AlertBox';
import { FormTextField } from '../../components/FormFields';
import withRequest from '../../utils/with-request';
import TermsOfUse from '../../components/TermsOfUse';
import EnterpriseUserService from '../../services/enterprise-user';
import AuthService from '../../services/auth';

const Signup = memo(({ formData, setAlert, alert, theme,
  setFormData, submitFormDataFetch, submitFormDataLoading,
  errors
}) => {
  const { mainColor } = theme;
  const handleForm = (attribute) => {
    setFormData({ ...formData, ...attribute });
  };
  const onSubmitForm = async () => {
    submitFormDataFetch(true);
  };
  const { memberName, memberLastname, memberEmail,
    memberPhone, companyName, password } = formData;
  const { showAlert, alertMsg, alertType } = alert;
  const onClearAlert = () => setAlert({ alertMsg: '', alertType: '', showAlert: false });
  let alertBox = null;
  if (showAlert) {
    alertBox = <AlertBox message={alertMsg} type={alertType} onClose={onClearAlert} />;
  }
  const disabledButton = _includes(_values(formData), '') || submitFormDataLoading;
  return (
    <div style={styles.container}>
      <div style={styles.content}>
        {alertBox}
        <img src={require('../../assets/images/logo.png')} width={150} alt={config.appName} style={styles.logo} />
        <Typography component="h1" variant="h5" style={styles.title}>
          Crie sua conta
        </Typography>
        <form onSubmit={onSubmitForm} style={styles.form}>
          <Grid container spacing={2}>
            <Grid item xs={12} md={6} style={styles.gridItem}>
              <FormTextField
                label="Seu nome*"
                disabled={submitFormDataLoading}
                value={memberName}
                onChange={e => handleForm({ memberName: e.target.value })}
              />
            </Grid>
            <Grid item xs={12} md={6} style={styles.gridItem}>
              <FormTextField
                label="Sobrenome*"
                disabled={submitFormDataLoading}
                value={memberLastname}
                onChange={e => handleForm({ memberLastname: e.target.value })}
              />
            </Grid>
            <Grid item xs={12} style={styles.gridItem}>
              <FormTextField
                error={errors.email}
                label="E-mail do trabalho*"
                disabled={submitFormDataLoading}
                value={memberEmail}
                onChange={e => handleForm({ memberEmail: e.target.value })}
              />
              {errors.email &&
                <FormHelperText style={styles.errorMessage}>
                  {errors.email}
                </FormHelperText>}
            </Grid>
            <Grid item xs={12} style={styles.gridItem}>
              <TextField
                variant="outlined"
                margin="normal"
                fullWidth
                label="Seu telefone com DDD*"
                type="tel"
                disabled={submitFormDataLoading}
                value={memberPhone}
                InputLabelProps={{ shrink: true }}
                onChange={e => handleForm({ memberPhone: (e.target.value || '').replace(/[\D]+/g, '').slice(0, 15) })}
              />
            </Grid>
            <Grid item xs={12} style={styles.gridItem}>
              <FormTextField
                label="Nome da empresa*"
                disabled={submitFormDataLoading}
                value={companyName}
                onChange={e => handleForm({ companyName: e.target.value })}
              />
            </Grid>
            <Grid item xs={12} style={styles.gridItem}>
              <TextField
                style={styles.textFieldMarginBottom}
                error={errors.password}
                variant="outlined"
                margin="normal"
                fullWidth
                label="Escolha sua senha*"
                placeholder="Senha (6 ou mais caracteres)"
                type="password"
                disabled={submitFormDataLoading}
                value={password}
                InputLabelProps={{ shrink: true }}
                onChange={e => handleForm({ password: e.target.value })}
              />
              {errors.password && <FormHelperText>{errors.password}</FormHelperText>}
            </Grid>
            <Grid item xs={12}>
              <Button
                disabled={disabledButton}
                fullWidth
                variant="contained"
                color="primary"
                size="large"
                style={styles.button}
                type="submit"
              >
                {submitFormDataLoading ?
                  <CircularProgress
                    color={mainColor}
                    variant="indeterminate"
                    disableShrink
                    thickness={5}
                  /> : 'CRIAR CONTA'}
              </Button>
            </Grid>
          </Grid>
        </form>
        <p>
          {'J possui conta? '}
          <Link to="/">Fazer login</Link>
        </p>
        <TermsOfUse />
      </div>
    </div>
  );
});

Signup.propTypes = {
  formData: PropTypes.object,
  setFormData: PropTypes.func,
  submitFormDataFetch: PropTypes.func,
  submitFormDataLoading: PropTypes.bool,
  errors: PropTypes.object,
  alert: PropTypes.object,
  theme: PropTypes.object,
  setAlert: PropTypes.func
};

const initialFormData = {
  memberName: '',
  memberLastname: '',
  memberEmail: '',
  memberPhone: '',
  companyName: '',
  password: ''
};

const withSubmitFormData = withRequest(AuthService.postSignup, {
  name: 'submitFormData',
  init: false,
  options: ({ formData, setAlert, history, setErrors, errors }) => ({
    variables: { ...formData, setErrors, errors },
    onRequestFinished: async () => {
      setAlert({
        alertMsg: 'Conta criada com sucesso, voc j ser redirecionado',
        alertType: 'success',
        showAlert: true
      });
      const { token } = await AuthService.postSignin(formData.memberEmail, formData.password);
      await Session.setUserToken(token);
      const enterpriseUserResponse = await EnterpriseUserService.getEnterpriseUser();
      const enterpriseUser = _get(enterpriseUserResponse, 'enterprise_user');
      Session.setUserData(enterpriseUser);
      history.push('/tickets');
    },
    onRequestError: (e) => {
      setErrors(_mapValues(_get(e, 'errors', {}), (err) => err[0]));
      setAlert({
        alertMsg: 'Erro ao criar conta',
        alertType: 'error',
        showAlert: true
      });
    }
  })
});

export default compose(
  memo,
  withStyles(styles, { withTheme: true }),
  withRouter,
  withState('formData', 'setFormData', initialFormData),
  withState('errors', 'setErrors', {}),
  withState('alert', 'setAlert', {
    alertMsg: '',
    alertType: '',
    showAlert: false
  }),
  withSubmitFormData
)(Signup);
