/* global window */
import React, { memo } from 'react';
import {
  compose, withState, withPropsOnChange,
  lifecycle, withHandlers
} from 'recompose';
import { withRouter } from 'react-router';
import PropTypes from 'prop-types';
import moment from 'moment';
import MomentUtils from '@date-io/moment';
import {
  sortBy as _sortBy, get as _get, find as _find, uniqBy as _uniqBy,
  map as _map, size as _size, isEmpty as _isEmpty,
  compact as _compact, mapValues as _mapValues
} from 'lodash';
import { withStyles, useTheme } from '@material-ui/core/styles';
import {
  useMediaQuery, Grid, Typography, InputAdornment, Select, MenuItem,
  Dialog, DialogContent, DialogActions, DialogTitle, IconButton, InputLabel,
  FormControl, FormControlLabel, FormHelperText, Switch, TextField, Button
} from '@material-ui/core';
import CloseIcon from '@material-ui/icons/Close';
import CircularProgress from '@material-ui/core/CircularProgress';
import CreateIcon from '@material-ui/icons/Create';
import { MuiPickersUtilsProvider, DatePicker } from '@material-ui/pickers';
import Loading from '../../../components/Loading';
import TicketsService from '../../../services/tickets';
import TicketCommentsService from '../../../services/ticket-comments';
import withRequest from '../../../utils/with-request';
import Tracker from '../../../utils/tracker';
import Session from '../../../utils/session';
import Screen from '../../../utils/screen';
import TicketsEnum from '../../../enums/tickets';
import { MEMBER_ROLE, AGENT_ROLES } from '../../../enums/user-roles';
import TicketComment from './TicketComment';
import CommentAttachButton from './CommentAttachButton';
import config from '../../../config';
import styles from './styles';

const {
  PRIORITIES, STATUSES, KANBAN_STATUSES
} = TicketsEnum;

const TicketModal = memo(({
  setAlert, classes, assignees, categories,
  open, onClose, setCurrentTicketState, history,
  currentTicket, formData: { validationErrors: errors },
  ticketLoading, theme, isEditing, setIsEditing, selectedAssignees, setSelectedAssignees,
  updateTicketFetch, updateTicketLoading, initialTitle, lastTitle,
  ticketCommentsFetch, ticketCommentsData, ticketCommentsLoading,
  commentMessage, setCommentMessage, setCloseModal, setInitialTitle,
  sendTicketCommentFetch, sendTicketCommentLoading, setLastTitle,
  moveTicketFetch, moveTicketLoading, archiveTicketFetch, archiveTicketLoading
}) => {
  if (!open) return null;
  const isMobile = Screen.isMobile();
  const enterpriseUser = Session.getUserData();
  const locations = _sortBy(_get(enterpriseUser, 'member.locations', []), ['name']);
  const memberRole = _get(enterpriseUser, 'member.role', MEMBER_ROLE);
  const isAgentUser = AGENT_ROLES.includes(memberRole);
  const comments = _sortBy(_get(ticketCommentsData, 'ticket_comments', []), ['created_at']).reverse();
  const requesterId = _get(currentTicket, 'requester.id');
  const memberId = _get(enterpriseUser, 'member.id');
  let statusValue = _get(currentTicket, 'status');
  const isArchived = Boolean(_get(currentTicket, 'archived_at'));
  const isMemberRequester = (requesterId && memberId && memberId === requesterId);
  const canEditForm = isAgentUser && !isArchived;
  const canComment = canEditForm || (isArchived ? false : isMemberRequester);
  const isCommentDisabled = !canComment || sendTicketCommentLoading || ticketCommentsLoading;
  const isFormLoading = updateTicketLoading || moveTicketLoading || ticketLoading;
  const isTitleDisabled = !isAgentUser || isFormLoading;
  const isFormDisabled = !canEditForm;
  const lockSendMessageButton = isCommentDisabled || _isEmpty(_get(commentMessage, 'content', ''));
  const requester = `${_get(currentTicket, 'requester.name', '')} ${_get(currentTicket, 'requester.lastname', '')}`;
  const createdAt = _get(currentTicket, 'created_at') ? moment(currentTicket.created_at).fromNow() : '';
  const code = _get(currentTicket, 'code', '');
  const title = _get(currentTicket, 'title', '');
  const ticketTitle = `${isArchived ? 'Ticket arquivado - ' : ''}${title}`;
  let priorityValue = _isEmpty(_get(currentTicket, 'priority')) ? '' : _get(currentTicket, 'priority');
  let locationValue = _get(currentTicket, 'location.id');
  let categoryValue = _get(currentTicket, 'category.id');
  const concatCategories = _compact([_get(currentTicket, 'category')].concat(categories || []));
  const allCategories = _sortBy(_uniqBy(concatCategories, 'id'), 'title');
  let deadlineValue = _get(currentTicket, 'deadline', null);
  const archiveButtonLoading = archiveTicketLoading || isFormLoading;
  const assigneesValue = _map(_get(currentTicket, 'assignees'), a => `${a.name} ${a.lastname}`).join(', ');
  if (isFormDisabled) {
    priorityValue = _get(_find(PRIORITIES, p => p.key === priorityValue), 'label', '');
    locationValue = _get(_find(locations, b => b.id === locationValue), 'name', '');
    categoryValue = _get(_find(categories, c => c.id === categoryValue), 'title', '');
    statusValue = _get(_find(STATUSES, s => s.key === statusValue), 'label', '');
    deadlineValue = _isEmpty(deadlineValue) ? '' : moment(deadlineValue).format('DD/MM/YYYY');
  }
  const themeForScreen = useTheme();
  const fullScreen = useMediaQuery(themeForScreen.breakpoints.down('sm'));
  const { mainColor } = theme;
  const handleClose = () => {
    const trackProps = { id: _get(currentTicket, 'id'), title: _get(currentTicket, 'title') };
    Tracker.hit('TicketModal - Closed', trackProps);
    setCloseModal(true);
    setIsEditing(false);
    onClose();
  };
  const handleAssigneesSelect = async (e) => {
    await setSelectedAssignees(e.target.value);
  };
  const handleAssigneesOnClose = async () => {
    const selectedAssigneesString = JSON.stringify(selectedAssignees.sort());
    const currentAssignees = _map(_get(currentTicket, 'assignees'), a => a.id);
    const currentTicketAssigneesString = JSON.stringify(currentAssignees.sort());
    if (selectedAssigneesString !== currentTicketAssigneesString) {
      setCloseModal(false);
      await updateTicketFetch(true);
      ticketCommentsFetch(true);
      const newAssignees = assignees.filter((a) => selectedAssignees.includes(a.id));
      setCurrentTicketState({ assignees: newAssignees });
    }
  };
  const handleChangeTicketState = async (attrs) => {
    if (canEditForm) {
      setCloseModal(false);
      setCurrentTicketState(attrs);
      await updateTicketFetch(true);
      ticketCommentsFetch(true);
    }
  };
  const handleArchive = () => {
    if (canEditForm) {
      if (window.confirm('Tem certeza em arquivar esse ticket?')) {
        archiveTicketFetch(currentTicket);
      }
    }
  };
  const handleChangeStatus = async (status) => {
    if (canEditForm) {
      setCloseModal(false);
      const params = { variables: { id: currentTicket.id, status } };
      setCurrentTicketState({ status });
      await moveTicketFetch(true, '', null, params);
      ticketCommentsFetch(true);
    }
  };
  const handleChangeBuilding = async (locationId) => {
    if (canEditForm) {
      setCloseModal(false);
      const params = { variables: { id: currentTicket.id, location_id: locationId } };
      setCurrentTicketState({ location: { id: locationId } });
      await moveTicketFetch(true, '', null, params);
      ticketCommentsFetch(true);
    }
  };
  const handleSendComment = () => {
    if (canComment) {
      setCloseModal(false);
      sendTicketCommentFetch(true);
    }
  };
  const handleEditTitle = () => {
    window.document.getElementById('title').focus();
    setLastTitle(title);
    setIsEditing(true);
  };
  const onChangeTicketTitle = (e) => {
    if (e.keyCode === 13 && !_isEmpty(title)) {
      handleChangeTicketState({ title });
      setInitialTitle(title);
      setIsEditing(false);
    } else if (e.keyCode === 27) {
      setCurrentTicketState({ title: initialTitle });
      setIsEditing(false);
    }
  };
  const onTicketTitleLooseFocus = () => {
    if (!_isEmpty(title) && lastTitle !== title) {
      handleChangeTicketState({ title });
      setInitialTitle(title);
    }
    setIsEditing(false);
  };
  const handleChangeTitle = (newTitle) => {
    if (canEditForm) {
      setCurrentTicketState({ title: newTitle });
    }
  };
  return (
    <Dialog
      open={open}
      fullScreen={fullScreen}
      onClose={handleClose}
      keepMounted
      fullWidth
      maxWidth="md"
      disableEscapeKeyDown={isEditing}
    >
      <Grid container>
        <Grid item xs={12} sm={12} md={5}>
          <DialogTitle>
            <TextField
              multiline
              id="title"
              disabled={isTitleDisabled}
              InputProps={{
                disableUnderline: true,
                style: {
                  color: '#000',
                  fontSize: 20,
                  width: isMobile ? 300 : 350,
                  backgroundColor: isEditing ? '#E0E0E0' : '',
                  borderRadius: isEditing ? 5 : '',
                  paddingLeft: 5
                },
                endAdornment: (
                  <InputAdornment position="end">
                    { isAgentUser && !isMobile && !isEditing &&
                      <IconButton onClick={handleEditTitle} disabled={isFormLoading}>
                        {
                          updateTicketLoading ?
                            <CircularProgress
                              color={mainColor}
                              className={classes.progress}
                              variant="indeterminate"
                              disableShrink
                              thickness={5}
                              size={25}
                            />
                            :
                            !isEditing && <CreateIcon fontSize="small" />
                        }
                      </IconButton> }
                  </InputAdornment>
                )
              }}
              value={ticketTitle}
              onFocus={handleEditTitle}
              onKeyDown={onChangeTicketTitle}
              onBlur={onTicketTitleLooseFocus}
              onChange={e => handleChangeTitle(e.target.value)}
            >
              {errors.title && <FormHelperText>Ttulo no pode ficar em branco</FormHelperText>}
            </TextField>
            <br />
            <Typography variant="body2" color="textSecondary" component="small">
              {ticketLoading ? '' : `#${code} - ${createdAt} por `}
              <b>{requester}</b>
            </Typography>
            <IconButton className={classes.closeButton} onClick={handleClose}>
              <CloseIcon />
            </IconButton>
          </DialogTitle>
          <DialogContent className={classes.ticketContent}>
            <FormControl fullWidth margin="normal" error={!!errors.status}>
              <TextField
                error={!!errors.status}
                label="Status"
                select={canEditForm}
                value={statusValue}
                disabled={isFormLoading}
                InputProps={{ readOnly: isFormDisabled }}
                InputLabelProps={{ shrink: true }}
                SelectProps={{ native: true }}
                onChange={e => handleChangeStatus(e.target.value)}
                variant="outlined"
              >
                {KANBAN_STATUSES.map(s => <option value={s.key} key={s.key}>{s.label}</option>)}
              </TextField>
              {errors.status && <FormHelperText>{errors.status}</FormHelperText>}
            </FormControl>
            {
              _size(locations) > 1 &&
                <FormControl fullWidth margin="normal" error={!!errors.location_id}>
                  <TextField
                    error={!!errors.location_id}
                    label="Local"
                    select={canEditForm}
                    value={locationValue}
                    disabled={isFormLoading}
                    InputProps={{ readOnly: isFormDisabled }}
                    InputLabelProps={{ shrink: true }}
                    SelectProps={{ native: true }}
                    onChange={e => handleChangeBuilding(e.target.value)}
                    variant="outlined"
                  >
                    {locations.map(b => <option value={b.id} key={b.id}>{b.name}</option>)}
                  </TextField>
                  {errors.location_id && <FormHelperText>{errors.location_id}</FormHelperText>}
                </FormControl>
            }
            <FormControl fullWidth margin="normal" error={!!errors.category_id}>
              <TextField
                error={!!errors.category_id}
                label="Categoria"
                select={canEditForm}
                value={categoryValue}
                disabled={isFormLoading}
                InputProps={{ readOnly: isFormDisabled }}
                SelectProps={{ native: true }}
                InputLabelProps={{ shrink: true }}
                onChange={e => handleChangeTicketState({ category: { id: e.target.value } })}
                variant="outlined"
              >
                {allCategories.map(c => <option key={c.id} value={c.id}>{c.title}</option>)}
              </TextField>
              {errors.category_id && <FormHelperText>{errors.category_id}</FormHelperText>}
            </FormControl>
            <FormControl
              fullWidth
              margin="normal"
              variant="outlined"
              className={classes.formControl}
              error={!!errors.assignees}
            >
              {
                isFormDisabled ?
                  <TextField
                    label="Responsveis"
                    value={assigneesValue}
                    disabled={isFormLoading}
                    InputProps={{ readOnly: isFormDisabled }}
                    InputLabelProps={{ shrink: true }}
                    variant="outlined"
                  />
                  :
                  <React.Fragment>
                    <InputLabel shrink>Responsveis</InputLabel>
                    <Select
                      error={!!errors.assignees}
                      label="Responsveis"
                      multiple
                      value={selectedAssignees}
                      onChange={handleAssigneesSelect}
                      onClose={handleAssigneesOnClose}
                      disabled={isFormLoading || isFormDisabled}
                    >
                      {assignees.map(l => (
                        <MenuItem key={l.id} value={l.id}>{`${l.name} ${l.lastname}`}</MenuItem>
                      ))}
                    </Select>
                    {errors.assignees && <FormHelperText>{errors.assignees}</FormHelperText>}
                  </React.Fragment>
              }
            </FormControl>
            <Grid container direction="row" justify="center" spacing={1}>
              <Grid item xs={6}>
                <FormControl fullWidth margin="normal" error={!!errors.deadline}>
                  {
                    isFormDisabled ?
                      <TextField
                        error={!!errors.deadline}
                        label="Prazo"
                        value={deadlineValue}
                        disabled={isFormLoading}
                        InputProps={{ readOnly: isFormDisabled }}
                        InputLabelProps={{ shrink: true }}
                        variant="outlined"
                      />
                      :
                      <MuiPickersUtilsProvider
                        libInstance={moment}
                        utils={MomentUtils}
                        locale={config.locale}
                      >
                        <DatePicker
                          autoOk
                          label="Prazo"
                          clearable
                          animateYearScrolling
                          disabled={isFormLoading}
                          InputProps={{ readOnly: isFormDisabled }}
                          invalidDateMessage="Formato invlido"
                          minDateMessage=""
                          variant="outlined"
                          inputVariant="outlined"
                          InputLabelProps={{ shrink: true }}
                          value={deadlineValue}
                          onChange={e => handleChangeTicketState({ deadline: e })}
                          minDate={new Date()}
                          format="DD/MM/YYYY"
                          cancelLabel="Cancelar"
                          clearLabel="Limpar"
                        />
                      </MuiPickersUtilsProvider>
                  }
                  {errors.deadline && <FormHelperText>{errors.deadline}</FormHelperText>}
                </FormControl>
              </Grid>
              <Grid item xs={6}>
                <FormControl fullWidth margin="normal" error={!!errors.priority}>
                  <TextField
                    error={!!errors.priority}
                    select={canEditForm}
                    label="Prioridade"
                    value={priorityValue}
                    disabled={isFormLoading}
                    InputProps={{ readOnly: isFormDisabled }}
                    InputLabelProps={{ shrink: true }}
                    SelectProps={{ native: true }}
                    onChange={e => handleChangeTicketState({ priority: e.target.value })}
                    variant="outlined"
                  >
                    {PRIORITIES.map(p => <option value={p.key} key={p.key}>{p.label}</option>)}
                  </TextField>
                  {errors.priority && <FormHelperText>{errors.priority}</FormHelperText>}
                </FormControl>
              </Grid>
            </Grid>
          </DialogContent>
          <DialogActions className={classes.ticketActions}>
            {
              !isArchived && isAgentUser &&
                <React.Fragment>
                  <Button
                    color="default"
                    size="large"
                    variant="outlined"
                    onClick={handleArchive}
                    disabled={archiveButtonLoading}
                  >
                    ARQUIVAR
                  </Button>
                  <Button
                    color="primary"
                    size="large"
                    variant="outlined"
                    onClick={() => history.push('/servicos/sobdemanda?utm_source=tickets')}
                  >
                    FAZER ORAMENTO
                  </Button>
                </React.Fragment>
            }
          </DialogActions>
        </Grid>
        <Grid item xs={12} sm={12} md={7} className={classes.bgCommentDialog}>
          <DialogTitle>Atividade</DialogTitle>
          <DialogContent className={classes.commentContent}>
            <div className={classes.commentArea}>
              {
                ticketCommentsLoading ?
                  <Loading />
                  :
                  _map(comments, c => <TicketComment key={c.id} comment={c} />)
              }
            </div>
            <div className={classes.commentHeaderContainer}>
              <Typography variant="h6">Comentar</Typography>
              {
                isAgentUser &&
                  <FormControlLabel
                    className={classes.isInternalSwitch}
                    control={
                      <Switch
                        checked={commentMessage.isInternal}
                        onChange={() => setCommentMessage({
                          ...commentMessage, isInternal: !commentMessage.isInternal
                        })}
                        color="primary"
                      />
                    }
                    label="Interno"
                    disabled={isCommentDisabled}
                  />
              }
            </div>
            <FormControl fullWidth margin="normal">
              <TextField
                variant="outlined"
                multiline
                rows={3}
                rowsMax={3}
                placeholder="Escreva seu comentrio aqui..."
                classes={{ root: classes.bgWhite }}
                InputLabelProps={{ shrink: true }}
                value={commentMessage.content}
                onChange={e => setCommentMessage({ ...commentMessage, content: e.target.value })}
                disabled={isCommentDisabled}
              />
            </FormControl>
          </DialogContent>
          <DialogActions className={classes.commentActions}>
            <CommentAttachButton
              ticketId={_get(currentTicket, 'id')}
              disabled={archiveButtonLoading}
              onAttachmentSent={() => ticketCommentsFetch(true)}
              onUploadError={() => {
                setAlert({
                  alertMsg: 'Erro ao tentar enviar arquivo.',
                  alertType: 'error',
                  showAlert: true
                });
              }}
            />
            <Button
              color="primary"
              size="large"
              variant="contained"
              onClick={handleSendComment}
              disabled={lockSendMessageButton}
            >
              ENVIAR
            </Button>
          </DialogActions>
        </Grid>
      </Grid>
    </Dialog>
  );
});

TicketModal.propTypes = {
  setAlert: PropTypes.func,
  open: PropTypes.bool,
  history: PropTypes.object,
  classes: PropTypes.object,
  onClose: PropTypes.func,
  setCloseModal: PropTypes.func,
  currentTicket: PropTypes.object,
  formData: PropTypes.object,
  initialTitle: PropTypes.string,
  setInitialTitle: PropTypes.func,
  lastTitle: PropTypes.string,
  setLastTitle: PropTypes.func,
  updateTicketFetch: PropTypes.func,
  updateTicketLoading: PropTypes.bool,
  setCurrentTicketState: PropTypes.func,
  ticketCommentsFetch: PropTypes.func,
  ticketCommentsData: PropTypes.object,
  ticketCommentsLoading: PropTypes.bool,
  ticketLoading: PropTypes.bool,
  moveTicketFetch: PropTypes.func,
  moveTicketLoading: PropTypes.bool,
  commentMessage: PropTypes.object,
  setCommentMessage: PropTypes.func,
  sendTicketCommentFetch: PropTypes.func,
  sendTicketCommentLoading: PropTypes.bool,
  archiveTicketFetch: PropTypes.func,
  archiveTicketLoading: PropTypes.bool,
  categories: PropTypes.array,
  assignees: PropTypes.array,
  theme: PropTypes.object,
  isEditing: PropTypes.bool,
  setIsEditing: PropTypes.func,
  selectedAssignees: PropTypes.array,
  setSelectedAssignees: PropTypes.func
};

const withTicketComments = withRequest(TicketCommentsService.getComments, {
  name: 'ticketComments',
  init: false,
  options: ({ ticket }) => ({ variables: _get(ticket, 'id') })
});

const withSendTicketComment = withRequest(TicketCommentsService.sendComment, {
  name: 'sendTicketComment',
  init: false,
  options: ({
    currentTicket, commentMessage,
    setCommentMessage, onClose, ticketCommentsFetch
  }) => ({
    variables: { id: _get(currentTicket, 'id'), ...commentMessage },
    onRequestFinished: () => {
      setCommentMessage(initialCommentState);
      ticketCommentsFetch(false);
    },
    onRequestError: err => {
      const trackProps = { id: _get(currentTicket, 'id'), title: _get(currentTicket, 'title') };
      Tracker.hit('TicketModal - Closed', trackProps);
      onClose(err, 'Erro ao enviar um comentrio para esse ticket');
    }
  })
});

const withMoveTicket = withRequest(TicketsService.moveTicket, {
  name: 'moveTicket',
  init: false,
  options: ({ onClose, closeModal, currentTicket, setFormData }) => ({
    onRequestFinished: () => {
      if (closeModal) {
        const trackProps = { id: _get(currentTicket, 'id'), title: _get(currentTicket, 'title') };
        Tracker.hit('TicketModal - Closed', trackProps);
        onClose(null, `Ticket #${currentTicket.code} atualizado com sucesso`);
      }
    },
    onRequestError: error => {
      const errors = _mapValues(_get(error, 'errors', {}), (err) => err[0]);
      if (_isEmpty(errors)) {
        const trackProps = { id: _get(currentTicket, 'id'), title: _get(currentTicket, 'title') };
        Tracker.hit('TicketModal - Closed', trackProps);
        const errorMessage = _get(error, 'error', 'Erro ao atualizar esse ticket');
        onClose(error, errorMessage);
      } else {
        setFormData({ validationErrors: errors });
      }
    }
  })
});

const withTicket = withRequest(TicketsService.getTicket, {
  name: 'ticket',
  init: false,
  options: ({ ticket, setCurrentTicket, onClose }) => ({
    variables: _get(ticket, 'id'),
    onRequestFinished: data => {
      setCurrentTicket(_get(data, 'ticket'));
    },
    onRequestError: err => {
      const trackProps = { id: _get(ticket, 'id'), title: _get(ticket, 'title') };
      Tracker.hit('TicketModal - Closed', trackProps);
      onClose(err, 'Erro ao abrir esse ticket');
    }
  })
});

const withUpdateTicket = withRequest(TicketsService.updateTicket, {
  name: 'updateTicket',
  init: false,
  options: ({ currentTicket, onClose, closeModal, setFormData, selectedAssignees }) => ({
    variables: { ticket: currentTicket, selectedAssignees },
    onRequestFinished: () => {
      setFormData({ validationErrors: {} });
      if (closeModal) {
        const trackProps = { id: _get(currentTicket, 'id'), title: _get(currentTicket, 'title') };
        Tracker.hit('TicketModal - Closed', trackProps);
        onClose(null, `Ticket #${currentTicket.code} atualizado com sucesso`);
      }
    },
    onRequestError: error => {
      const errors = _mapValues(_get(error, 'errors', {}), (err) => err[0]);
      setFormData({ validationErrors: errors });
    }
  })
});

const withArchiveTicket = withRequest(TicketsService.archiveTicket, {
  name: 'archiveTicket',
  init: false,
  options: ({ currentTicket, onClose }) => ({
    variables: { ticketsIds: [currentTicket.id] },
    onRequestFinished: () => {
      const trackProps = { id: _get(currentTicket, 'id'), title: _get(currentTicket, 'title') };
      Tracker.hit('Ticket - Archived', trackProps);
      onClose(null, `Ticket #${currentTicket.code} arquivado com sucesso`);
    },
    onRequestError: err => {
      onClose(err, `Erro ao arquivar ticket #${currentTicket.code}`);
    }
  })
});

const initialFormData = { validationErrors: {} };
const initialCommentState = { content: '', isInternal: false };
export default compose(
  withStyles(styles, { withTheme: true }),
  withRouter,
  withState('formData', 'setFormDataRaw', initialFormData),
  withHandlers({
    setFormData: ({ setFormDataRaw, formData }) => (attrs) => {
      setFormDataRaw({ ...formData, ...attrs });
    }
  }),
  withState('currentTicket', 'setCurrentTicket', {}),
  withState('commentMessage', 'setCommentMessage', initialCommentState),
  withState('selectedAssignees', 'setSelectedAssignees', []),
  withState('closeModal', 'setCloseModal', false),
  withState('isEditing', 'setIsEditing', false),
  withState('initialTitle', 'setInitialTitle', ''),
  withState('lastTitle', 'setLastTitle', ''),
  withTicketComments,
  withSendTicketComment,
  withUpdateTicket,
  withMoveTicket,
  withArchiveTicket,
  withTicket,
  lifecycle({
    componentDidMount() {
      const { ticket, setCurrentTicket, setCommentMessage, setSelectedAssignees } = this.props;
      setSelectedAssignees(_map(_get(ticket, 'assignees'), a => a.id));
      setCurrentTicket(ticket);
      setCommentMessage(initialCommentState);
    }
  }),
  withPropsOnChange(['open'], ({
    open, setCommentMessage, setCurrentTicket, setFormData, setSelectedAssignees,
    ticketFetch, ticketCommentsFetch, ticket, setInitialTitle
  }) => {
    if (open) {
      const trackProps = { id: _get(ticket, 'id'), title: _get(ticket, 'title') };
      Tracker.hit('TicketModal - Opened', trackProps);
      if (ticket && !ticket.mark_as_read) {
        TicketsService.markAsRead(ticket.id);
      }

      setFormData(initialFormData);
      setSelectedAssignees(_map(_get(ticket, 'assignees'), a => a.id));
      setCurrentTicket({});
      setInitialTitle(_get(ticket, 'title'));
      setCommentMessage(initialCommentState);
      ticketFetch(true);
      ticketCommentsFetch(true);
    }
  }),
  withHandlers({
    setCurrentTicketState: ({ setCurrentTicket, currentTicket }) => (attrs) => {
      setCurrentTicket({ ...currentTicket, ...attrs });
    }
  })
)(TicketModal);
