import React, { memo } from 'react';
import { groupBy, map, get } from 'lodash';
import { compose } from 'recompose';
import { withStyles } from '@material-ui/core/styles';
import { Typography, Grid } from '@material-ui/core';
import PropTypes from 'prop-types';

import Loading from '../../../components/Loading';
import SERVICE_TYPES from '../service-types';
import ServiceService from '../../../services/service';
import withRequest from '../../../utils/with-request';
import ServiceOnDemandCard from './ServiceOnDemandCard';
import styles from './styles';

const OnDemand = memo(({
  classes, servicesData, servicesLoading: loading
}) => {
  if (loading) {
    return (
      <Loading />
    );
  }

  const groupedServices = groupBy(get(servicesData, 'services', []), 'category');

  return map(groupedServices, (services, categoryName) => (
    <div className={classes.categoryContainer} key={categoryName}>
      <Typography className={classes.categoryName} variant="h6">
        <b>{categoryName}</b>
      </Typography>
      <Grid container spacing={2}>
        {
          map(services, service => (
            <Grid key={service.id} item xs={12} sm={4} md={3}>
              <ServiceOnDemandCard service={service} />
            </Grid>
          ))
        }
      </Grid>
    </div>
  ));
});

OnDemand.propTypes = {
  classes: PropTypes.object,
  servicesData: PropTypes.object,
  servicesLoading: PropTypes.bool
};

const withServices = withRequest(ServiceService.getServices, {
  name: 'services',
  options: ({ setAlert }) => ({
    variables: { serviceType: SERVICE_TYPES.ON_DEMAND },
    onRequestError: () => {
      setAlert({
        alertMsg: 'Erro ao carregar dados do servidor',
        alertType: 'error',
        showAlert: true
      });
    }
  })
});

export default compose(
  withStyles(styles, { withTheme: true }),
  withServices
)(OnDemand);
